import Vue from 'vue';
import main from './message.vue';

let Constructor=Vue.extend(main);
let newMessage;

const Message=function(val){
    let obj={show:false};
    // data  
    if(typeof val === 'string'){
         obj.message=val;  
         obj.duration=3000; 
    }else{
        obj=Object.assign(obj,val);
        if(!obj.duration){
            obj.duration=3000;
        }
    }
    newMessage = new Constructor({
        data:obj
    });
    // 
    let vm = newMessage.$mount()
    let el = vm.$el
    document.body.appendChild(el) // dombody
    vm.show = true
    // 
    setTimeout(()=>{
        vm.show=false;
        document.body.removeChild(el) //bodydom
        newMessage.$destroy()
        vm = null // nulljs
    },obj.duration)
}

export default Message